<template>
  <div id="artTopFind">
    <top-find>
      <div slot="find-input">
        文章标题:<input type="text" v-model="findTitle" placeholder="请输入文章标题" />
      </div>

      <button slot="btn" @click="find" >查询</button>
    </top-find>
  </div>
</template>

<script>
import TopFind from '@/components/children/TopFind'

export default {
  name:'ArtTopFind',
  data(){
      return {
          findTitle:''
      }
  },
  components:{
    TopFind
  },
  methods:{
      find(){
  
        this.$emit('findArt', this.findTitle)
      }
  }

}
</script>

<style scoped>
  
</style>


