<template>
  <div id="artCommentAdd">
    <div class="top">
      评论编辑
    </div>
    <div class="comment-edit">
      <div>
        <editor @commitArt="commitArt"></editor>

        <div class="btn">
          <el-button type="primary" @click="commit">提交</el-button>
          <el-button type="" @click="back">返回</el-button>
        </div>
      </div>
     
    </div>
  </div>
</template>

<script>
import Editor from '@/components/common/ue'

export default {
  data(){
    return{
      comment:{
        id:0,
        commenter:'jj',
        commentArt:'',
        commentContent:'',
        registerTime:'',
        commentTime:'',
        thinkGood:0,
      },
      editorOption:{}
    }
  },
  created(){
    this.comment.commentArt = this.$route.query.artTitle
    this.comment.id = this.$store.state.currentComment.length
  },
  components: {
    Editor
　},
  computed:{
    getDate(){
      let time = new Date();
      return time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate()
    }
  },
  methods:{
    commitArt(content){
      this.comment.commentContent = content;
    },

    // 提交
    commit(){
      let reg = /\s+/

      if(this.comment.commentContent ==='' || reg.test(this.comment.commentContent)){
        alert('文章内容不能为空')
      }else{
        this.comment.commentTime = this.getDate
        this.$store.commit('addComment',this.comment)
        console.log(this.comment)
        this.$router.push({path:'/home/artDetail', query:{selectIndex:this.$route.query.artIndex}})
      }

    },

    back(){
      this.$router.go(-1)
    }
  }
}
</script>

<style>
#artCommentAdd{
  width:100%;
}

.top{
  width:80%;
  border-radius: 3px;
  padding-left:20px;
  height:2rem;
  line-height: 2rem;
  background-color: rgb(73, 156, 233);
  color:white;
}

.comment-edit{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:10px;
}


.btn{
    width:80%;
    display: flex;
    justify-content: center;
  }

  .btn button{
    margin-top:100px;
  }
</style>


