<template>
  <div id="artDetail">
    <art-detail-content :art="art" />
    <div class="comment-divider">
      <span>当前：所有文章 --> 评论</span>
      <div class="operation">
        <el-button type="success" size="mini" @click="addComment">评论</el-button>
        <el-button type="danger" size="mini">删除</el-button>
        <el-button type="info" size="mini" @click="back">返回</el-button>
      </div>

    </div>
    <art-comment-display :comments="comments"></art-comment-display>
  </div>
</template>

<script>
import ArtDetailContent from './artDetailChildren/ArtDetailContent'
import ArtCommentDisplay from './artDetailChildren/ArtCommentDisplay'

export default {
  name:'ArtDetail',
  data(){
    return{
      index:0,
      art:{},
      comments:[],
    }
  },
  components:{
    ArtDetailContent,
    ArtCommentDisplay
  },
  created(){
    this.getArt()
    this.getComments()

    // 每次页面刷新后无法获取添加数据
    console.log(this.$store.state.currentComment.length)
  },
  methods:{
    // 获取文章信息
    getArt(){
      // let page = parseInt(this.$route.query.onePageNum)
      // let count = parseInt(this.$route.query.currentPage - 1)
      // let index = parseInt(this.$route.query.index)
      // this.index =  page * count + index
      this.index = this.$route.query.selectIndex
      this.$store.commit('getOneArt',this.index)
      this.art = this.$store.state.detailArt
    },

    // 获取当前文章评论
    getComments(){
      this.$store.commit('getCurrentComment',this.art.artTitle)
      this.comments = this.$store.state.currentComment
    },
    
    back(){
      this.$router.push('/home/art')
    },

    addComment(){
      this.$router.push({path:'/home/artCommentAdd',query:{artTitle:this.art.artTitle,artIndex:this.index}})
    }
  }
}
</script>

<style scoped>
  #artDetail{
    width:80%;
  }

  .comment-divider{
    margin-top:5px;
    padding:0 10px;
    display: flex;
    height:50px;
    justify-content: space-between;
    align-items: center;
    border:1px solid #efefef;
  }
</style>


