import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'
import All from '@/components/menu/All'
import Avatar from '@/components/menu/Avatar'
import Comment from '@/components/menu/Comment'
import Art from '@/components/menu/Art'
import MySetting from '@/components/menu/MySetting'
import User from '@/components/menu/User'
import Voice from '@/components/menu/Voice'

import ArtAdd from '@/components/menu/artChildren/ArtAdd'
import ArtDetail from '@/components/menu/artChildren/ArtDetail'
import ArtCommentAdd from '@/components/menu/artChildren/artDetailChildren/ArtCommentAdd'
import UserEdit from '@/components/menu/userChildren/UserEdit'
Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect:'/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path:'/home',
      name:'Home',
      component:Home,
      children:[
        {
          path: 'all',
          name: 'All',
          component: All,
        },
        {
          path: 'art',
          name: 'Art',
          component: Art,
        },
        {
          path:'artAdd',
          component:ArtAdd
        },
        {
          path:'artDetail',
          component:ArtDetail
        },
        {
          path:'artCommentAdd',
          component:ArtCommentAdd
        },
        {
          path: 'voice',
          name: 'Voice',
          component: Voice,
        },
        {
          path: 'mySetting',
          name: 'MySetting',
          component: MySetting,
        },
        {
          path: 'avatar',
          name: 'Avatar',
          component: Avatar,
        },
        {
          path: 'user',
          name: 'User',
          component: User,
        },
        {
          path: 'userEdit',
          name: 'userEdit',
          component: UserEdit,
        },
        {
          path: 'comment',
          name: 'Comment',
          component: Comment,
        },
      ]
    },
  ],
})
// router.beforeEach((to, from, next) => {
//     if (to.matched.some((r) => r.meta.requireAuth)) {
//       if (AV.User.current().getSessionToken()) {   //判断是否已经登录
//         next()
//       } else {
//         next({
//           path: '/login',                //无登录状态,先跳转到登录页面
//           query: { redirected: to.fullPath }
//           // 登录成功后跳转回到该路由
//         });
//       }
//     } else {
//       next()
//     }
// });
export default router;




