<template>
  <div id="artCommentDisplay">
    <art-display-item class="item" v-for="(item,index) in comments" :key="index">
      <span slot="top-title">热门评论</span>
      <img slot="avatar" alt="img" src="@/assets/avatar17.svg" />
      <div slot="info">
        <p>评论人：{{item.commenter}}</p>
        <p>评论人注册时间：{{item.registerTime}}</p>
        <p>评论时间：{{item.commentTime}}</p>
        <div>删除选中:<input type="checkbox" /></div>
      </div>
      <span slot="content">
        {{item.commenter}}说: {{item.commentContent}}
          
      </span>
    </art-display-item>
  </div>
</template>

<script>
import ArtDisplayItem from './ArtDisplayItem'

export default {
  name:'ArtCommentDisplay',
  components:{
    ArtDisplayItem
  },
  props:{
    comments:{
      type:Array,
      default(){
        return []
      }
    }
  }
}
</script>

<style scoped>
  #artCommentDisplay{
    margin-top:10px;
    border:3px solid rgb(13, 69, 134);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .item{
    width:95%;
  }
</style>


