<template>
    <div class="out" v-if="showSignUp">
        <el-container class="cont" style="height:700px">
            <el-main class="main">
                <el-row type="flex" justify="center" align="end" class="row">
                    <el-col :span="6">
                        <el-form ref="ruleForm" :model="ruleForm" label-width="80px" class="form" :rules="rules">
                            <el-form-item label="用户名" prop="name">
                                <el-input v-model="ruleForm.name"></el-input>
                            </el-form-item>
                            <el-form-item label="密码" prop="pwd">
                                <el-input v-model="ruleForm.pwd" type="password"></el-input>
                            </el-form-item>
                            <el-form-item label="确认密码" prop="cpwd">
                                <el-input v-model="ruleForm.cpwd" type="password"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-select v-model="ruleForm.region" placeholder="请选择性别" prop="region"
                                    style="width:230px">
                                    <el-option label="男" value="man"></el-option>
                                    <el-option label="女" value="woman"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="年龄" prop="age" :rules="[
                                    { required: true, message: '年龄不能为空'},
                                    { type: 'number', message: '年龄必须为数字值'}
                                    ]">
                                <el-input v-model.number="ruleForm.age" type="age"></el-input>
                            </el-form-item>
                            <el-form-item label="邮箱"
                                prop="email"
                                :rules="[
                                { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                                { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
                                ]">
                                <el-input v-model="ruleForm.email"></el-input>
                            </el-form-item>
                            <el-form-item style="margin-left:45px">
                                <el-button type="primary" @click="register">注册</el-button>
                                <el-button type="primary" @click="back">返回</el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </div>
</template>

<script>
 import AV from 'leancloud-storage'
    export default {
        methods: {
            back() {
                this.$emit('changeLogin')
            },
            register(){
                   const user = new AV.Object('_user')
                   user.set('username',this.ruleForm.name)
                   user.set('age',this.ruleForm.age)
                   user.set('email',this.ruleForm.email)
                   user.set('sex',this.ruleForm.region)
                   user.set('password',this.ruleForm.pwd)
                   user.save().then((user)=>{
                        console.log(`保存成功。objectId：${user.id}`)
                        this.$emit('changeLogin')
                   },(error)=>{
                       this.$message({
                        message:error,
                        type: 'error'
                    });
                   })
            }
        },
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.ruleForm.cpwd !== '') {
                        this.$refs.ruleForm.validateField('cpwd');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.ruleForm.pwd) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                rules: {
                    name: [{
                        required: true,
                        message: '请输入用户名',
                        trigger: 'blur'
                    }, ],
                    pwd: [{
                        validator: validatePass,
                        trigger: 'blur'
                    }],
                    cpwd: [{
                        validator: validatePass2,
                        trigger: 'blur'
                    }],
                },
                ruleForm: {
                    name: '',
                    pwd: '',
                    cpwd: '',
                    age: '',
                    email: '',
                    region: '',
                },

            }
        },
        props: {
            showSignUp: {
                type: Boolean,
                default: false
            },
        },
    }
</script>

<style lang="stylus" scoped>
        .main
            top 200px
            height 100%
            width 100%
            background-color white
            margin-bottom 400px
            .form 
                background-color white 
                padding-top 50px
                padding-right 50px
                padding-bottom 10px 
                transition: box-shadow 0.5s;
                -webkit-transition: box-shadow 0.5s;
                :hover
                   opacity: 1;
</style>



