<template>
  <div id="userEdit">
    <div class="title">用户信息编辑</div>
    <div  class="input">
      <table>
        <tr>
          <td>用户名：</td>
          <td><input type="text" v-model="user.name" :placeholder="user.name" /></td>
        </tr>
        <tr>
          <td>密码：</td>
          <td><input type="text" v-model="user.pwd" :placeholder="user.pwd" /></td>
        </tr>
        <tr>
          <td>性别：</td>
          <td>
            <select v-model="user.sex" placeholder="性别">
              <option label="女" value="女"></option>
              <option label="男" value="男"></option>
            </select>
          </td>
        </tr>
        <tr>
          <td>年龄：</td>
          <td><input type="text" v-model="user.age" :placeholder="user.age" /></td>
        </tr>
        <tr>
          <td>邮箱：</td>
          <td><input type="text" v-model="user.email" :placeholder="user.email" /></td>
        </tr>
        <tr>
          <td>权限：</td>
          <td>
            <select v-model="user.power" placeholder="权限">
              <option label="普通用户" value="普通用户"></option>
              <option label="管理员" value="管理员"></option>
            </select>
          </td>
        </tr>
      </table>
    </div>

    <div class="btn" >
      <el-button type="primary" @click="changeUser" size="mini">修改</el-button>
      <el-button type="info" @click="back" size="mini">返回</el-button>
    </div>

    <el-divider></el-divider>
  </div>
</template>

<script>
export default {
  name:'UserEdit',
  data(){
    return {
      index:0,
      user:{}
    }
  },
  created(){
    this.index = this.$route.query.selectIndex
    this.user = this.$store.state.user[this.index]
  },
  methods:{
    submitUser(){
      let reg = /\s+/

      for(item in this.user){
        if(reg.test(item)){
          alert(item + '不能为空')
          return
        }
      }

    },

    changeUser(){
      this.$store.commit('changeUser',this.user,this.index)
      this.$router.replace('/home/user')
    },

    back(){
      this.$router.replace('/home/user')
    }
  }
}
</script>

<style scoped>

  #userEdit{
    width:80%;
  }

  .title{
    width: 100%;
    height:44px;
    line-height: 44px;
    padding-left:20px;
    background-color: cornflowerblue;
    color: white;
    border-radius: 3px;
  }

  .input{
    width:100%;
    display: flex;
    justify-content: center;
    text-align: right;
  }

  .input tr{
    height:3rem;
  }

  .input input,select{
    border:1px solid #efefef;
    height:2rem;
    width:800px;
  }

  .btn{
    margin-top:20px;
    display: flex;
    justify-content: center;
  }
</style>


