<template>
    <div id='art'>
        <art-top-find @findArt="findArt" />
        <art-list @addArt="addArt" :currentMode="currentListMode" :content="content" />
    </div>
</template>

<script>
import ArtTopFind from './artChildren/ArtTopFind'
import ArtList from './artChildren/ArtList'

export default {
    name:'Art',
    data(){
        return {
            currentListMode:'所有文章',
            content:[]
        }
    },
    created(){
        // 获取所有文章
        this.content = this.getAllContent
    },
    components:{
        ArtTopFind,
        ArtList,
    },
    computed:{
        getAllContent(){
          return this.$store.state.content
        }
    },
    methods:{
        addArt(){
            this.$router.push('/home/artAdd')
        },
        findArt(title){
            let reg = /\s+/
            if(title === '' || reg.test(title)){
                this.$router.go(0)
            }else{
                this.currentListMode = '查询文章：' + title
                this.$store.commit('getArt',title)
                this.content = this.$store.state.findArts
            }

        },
    }
}
</script>

<style  scoped>
    #art{
        width:80%;
        color:grey;
    }

    
</style>


