<template>
  <div id="artList">
    <list-display :content="content" @jump="jump">
      <span slot="title">
        当前：{{currentMode}}
      </span>
      <span slot="btn">
        <el-button type="success" size="mini" @click="addArt">新增</el-button>
        <el-button type="danger" size="mini">删除</el-button>
      </span>

      <table slot="table">
        <th></th>
        <th>阅读人数</th>
        <th>评论人数</th>
        <th>文章标题</th>
        <th>文章作者</th>
        <th>发表日期</th>
        <th>最后评论</th>

        <tr  v-for="(item,index) in currentContent" :key="index">
          <td class="check"><input type="checkbox" /></td>
          <td @click="toDetailArt(index)" v-for="(con,indey) in item" :style="{'display': indey === 'artContent'? 'none':'table-cell'}" :key="indey">
            {{con}}
          </td>
        </tr>
      </table>
    </list-display>
  </div>
</template>

<script>
import ListDisplay from '@/components/children/ListDisplay'

export default {
  name:'ArtList',
  components:{
    ListDisplay
  },
  data(){
    return {
      
      currentPage:1,  // 当前页数
      currentContent:[],  // 当前页显示内容
      onePageNum:7, //每一页显示数量
      totalNum:1, // 内容总数
      totalPage:1,  // 总页数
      jumpPage:1, //跳转页面
    }
  },
  props:{
    content:{
      type:Array,
      default(){
        return []
      }
    }, // 所有文章
    currentMode:{
      type:String,
      default:''
    } // 当前模块
  },
  created(){

    // 分页
    this.totalNum = this.content.length
    this.totalPage = parseInt(this.totalNum%this.onePageNum === 0? this.totalNum / this.onePageNum : this.totalNum / this.onePageNum + 1)
    this.speratePage()

  },
  methods:{
   

    // 分页
    speratePage(){

      // 该页内容开始位置
      let startNum = (this.currentPage - 1) * this.onePageNum
      let endNum = 0
      
      // 是否为最后一页
      if(this.currentPage === this.totalPage){
        endNum = this.totalNum
      }else{
        endNum = startNum + this.onePageNum
      }

      this.currentContent = this.content.slice(startNum,endNum)
    },

    jump(content){
      this.currentContent = content
    },

    addArt(){
      this.$emit('addArt')
    },


    toDetailArt(index){
      let selectIndex =  this.onePageNum * (this.currentPage - 1) + index
      this.$router.push({path:'/home/artDetail', query:{selectIndex}})
    }
  }
}
</script>

<style scoped>

  table{
    width:90%;
    border-radius: 3px;
  }

  table,th,tr,td{
    border:1px solid rgb(206, 205, 205);
    border-collapse: collapse;
    border-radius:3px;
  }

  tr,th{
    height:3rem;
    line-height: 3rem;
    text-align: center;
  }

  #artList{
    border-radius: 3px;
    margin-top:10px;
    border:1px solid rgb(206, 205, 205);
  }

  .list-top{
    border-bottom: 1px solid rgb(206, 205, 205);
    padding:10px 10% 10px 2%;
    display: flex;
    justify-content: space-between;
  }

  .list-content{
    margin:20px;
    display: flex;
    justify-content: center;
  }

  .list-content .check{
    width:3rem;
  }

  .page-jump{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .page-jump button{
    width:60px;
    height:30px;
    border-radius:3px;
    color:white;
    background-color: rgb(69, 115, 201);
    margin:10px;
  }

  .page-jump input{
    border:1px solid #aabbaa;
    height:30px;
  }

  .page-jump span{
    color: rgb(141, 140, 140);
    padding:0 10px 0;
  }

</style>


