<template>
  <el-container class="cont" style="height:700px">
    <el-main class="main" >
      <el-row type="flex" justify="center" align="end" class="row" v-if="showLogin">
        <el-col :span="6">
          <el-form ref="ruleForm" :model="ruleForm" label-width="80px" class="form" :rules="rules" >
            <el-form-item label="用户名" prop="name">
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pwd">
              <el-input v-model="ruleForm.pwd" type="password"></el-input>
            </el-form-item>
            <el-form-item style="margin-left:45px">
              <el-button type="primary" @click="login">登录</el-button>
              <el-button type="primary" @click="signUp">注册</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row>
        <SignUp :showSignUp="this.showSignUp" @changeLogin="changeLogin"></SignUp>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
  import SignUp from '../components/children/SignUp'
  export default {
    name: 'Login',
    components:{
      SignUp
    },
    data: () => ({
      ruleForm: {
        name: '',
        pwd: ''
      },
      rules:{
          name: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
          ],
          pwd: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ],
      },
      showSignUp:false,
      showLogin:true,
    }),
    mounted() {
      
    },
    methods:{
      login(){
        this.$router.push('/home/all')
      },
       signUp(){
        this.showSignUp=true,
        this.showLogin=false
      },
      changeLogin(change){
        this.showLogin = true
        this.showSignUp =false  
      }
  }
  }
</script>

<style lang="stylus" scoped>
  .main
    padding-top 100px
    height 100%
    width 100%
    background-color white
    .form 
      background-color white
      padding-top 50px
      padding-right 50px
      padding-bottom 10px 
      color: white;
      font-weight: bold;
      opacity: 0.9;
      transition: opacity 0.5s;
      -webkit-transition: opacity 0.5s;
      opacity 0.9
</style>
<style lang="stylus" scoped>
  .body
    color red
</style>



