<template>
  <div id="art-add">
    <div class="top">
      文章编辑
    </div>
    <div class="art-edit">
      <div class="art-title">
        文章标题：<input type="text" v-model="art.artTitle" />
      </div>

      <div>
        <editor @commitArt="commitArt"></editor>

        <div class="btn">
          <el-button type="primary" @click="commit">提交</el-button>
          <el-button type="" @click="back">返回</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Editor from '@/components/common/ue'

export default {
  name:'ArtAdd',
  data(){
    return{
      art:{
        readPeople:0,
        commentPeople:0,
        artTitle:'',
        author:'',
        artContent:'',
        deliverDate:'',
        finalCommentTime:''
      },
      editorOption:{}
    }
  },
  components: {
    Editor
　},
  computed:{
    getDate(){
      let time = new Date();
      return time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate()
    }
  },
  methods:{
    commitArt(content){
      this.art.artContent = content;
    },

    // 提交
    commit(){
      let reg = /\s+/

      if(this.art.artTitle==='' || reg.test(this.art.artTitle)){
        alert('文章标题不能为空')
      }else if(this.art.artContent ==='' || reg.test(this.art.artContent)){
        alert('文章内容不能为空')
      }else{
        this.art.deliverDate = this.getDate
        this.$store.commit('addArt',this.art)
        this.$router.replace('/home/art')
      }

    },

    back(){
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
#art-add{
  border-radius: 3px;
}

.top{
  width:80%;
  border-radius: 3px;
  padding-left:20px;
  height:2rem;
  line-height: 2rem;
  background-color: rgb(73, 156, 233);
  color:white;
}

.art-edit{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding:10px;
}

.art-title{
  display: flex;
  width:75%;
}

.art-title input{
  width:80%;
  border:1px solid grey;
}

.btn{
    width:80%;
    display: flex;
    justify-content: center;
  }

  .btn button{
    margin-top:100px;
  }
</style>


