<template>
  <div id="artDetailContent">
    <art-display-item>
      <span slot="top-title">文章</span>
      <img slot="avatar" alt="img" src="@/assets/avatar17.svg" />
      <div slot="info">
        <p>主题：{{art.artTitle}}</p>
        <p>发帖人：{{art.author}}</p>
        <p>发帖人注册时间：{{art.deliverDate}}</p>
        <p>发帖时间：{{art.deliverDate}}</p>
      </div>
      <span slot="content">{{art.artContent}}</span>
    </art-display-item>
  </div>
</template>

<script>
import ArtDisplayItem from './ArtDisplayItem'

export default {
  name:'ArtDetailContent',
  components:{
    ArtDisplayItem
  },
  props:{
    art:{
      type:Object,
      default(){
        return {}
      }
    }
  }
}
</script>

<style scoped>
 
</style>


