<template>
    <div>
        <el-container>
            <el-header class="head">
                <el-row :gutter="24">
                    <el-col :offset="10" style="margin-top:20px" :span="2">所有文章</el-col>
                    <el-col :span="1" :offset="9" style="margin-top:13px">
                        <el-button @click="newArtical">新建</el-button>
                    </el-col>
                </el-row>
            </el-header>
            <el-main>
                <el-table :data="tableData" stripe style="width: 100%">
                    <el-table-column prop="attributes.read" label="阅读人数" width="180">
                    </el-table-column>
                    <el-table-column prop="attributes.comment.length" label="评论人数" width="180">
                    </el-table-column>
                    <el-table-column prop="attributes.title" label="文章标题">
                    </el-table-column>
                    <el-table-column prop="attributes.author" label="文章作者">
                    </el-table-column>
                    <el-table-column prop="createdAt" label="发表时间">
                    </el-table-column>
                    <el-table-column prop="updatedAt" label="最后评论">
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)" >编辑</el-button>
                            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="new" v-if="show">
                    <el-form ref="form" :model="form" label-width="80px">
                        <div style="position:relative;top:-30px;left:190px">新建文章</div>
                        <el-form-item label="文章标题">
                            <el-input v-model="form.title"></el-input>
                        </el-form-item>
                        <el-form-item label="文章内容">
                            <el-input v-model="form.text" type="textarea" ></el-input>
                        </el-form-item>
                        <div style="margin-left:150px;position:relative;top:20px">
                            <el-button type="primary" @click="en">确定</el-button>
                            <el-button type="primary" @click="cancle">取消</el-button>
                        </div>
                    </el-form>
                </div>
                 <div class="new" v-if="edit">
                    <el-form ref="form" :model="editDate" label-width="80px">
                        <div style="position:relative;top:-30px;left:190px">文章编辑</div>
                        <el-form-item label="文章标题">
                            <el-input v-model="editDate.title"></el-input>
                        </el-form-item>
                        <el-form-item label="文章内容">
                            <el-input v-model="editDate.text" type="textarea" ></el-input>
                        </el-form-item>
                        <div style="margin-left:150px;position:relative;top:20px">
                            <el-button type="primary" @click="saveDate">确定</el-button>
                            <el-button type="primary" @click="cancleDate">取消</el-button>
                        </div>
                    </el-form>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
     import AV from 'leancloud-storage'
    export default {
        data() {
            return {
                tableData: [{

                }],
                form: {
                    title: '',
                    text: '',
                    userId:AV.User.current().id,
                    username:AV.User.current().attributes.username,
                },
                editDate:{
                    title: '',
                    text: '',
                },
                show: false,
                edit:false,
                artId:'',
            }
        },
        methods: {
            handleEdit(index, row) {
                console.log(index, row);
                this.edit=true
                this.editDate.title=row.attributes.title
                this.editDate.text = row.attributes.text
                this.artId = row.attributes.id
            },
            handleDelete(index, row) {
                console.log(index, row);
            },
            cancle() {
                this.show = false
            },
            cancleDate(){
                this.edit =false
            },
            en(){
                const art = new AV.Object('Artical')
                art.set('title',this.form.title)
                art.set('text',this.form.text)
                art.set('userId',this.form.userId)
                art.set('author',this.form.username)
                art.set('read',0)
                art.set('comment',[])
                art.save().then((art) => {
                    const query = new AV.Query('Artical')
                    query.find().then((artData)=>{
                    this.tableData = artData
                })
                console.log(`保存成功。objectId：${art.id}`);
                }, (error) => {
                // 异常处理
                });
                this.show = false
                //更新数据
            },
             newArtical() {
                this.show = true  
             }, 
            saveDate(){
                const art = AV.Object.createWithoutData('Artical', this.artId);
                art.set('title', this.editDate.title)
                art.set('text',this.editDate.text)
                art.save();
            },
            
        },
        created(){
            const query = new AV.Query('Artical')
            query.find().then((artData)=>{
                console.log(artData)
                this.tableData = artData
            })
        }
    }
</script>

<style lang="stylus" scoped>
    .head 
         background-image url('../../assets/weather.png')
         background-size: 100% 100%;
</style>
<style  scoped>
    .new{
        position:absolute;
        opacity: 1.0; 
        height: 300px;
        width: 500px;
        left: 350px;
        top:100px;
        padding: 60px;
        background-color: aliceblue;
    }

</style>


