<template>
  <div id="artDisplayItem">
    <div class="top-title"><slot name="top-title"></slot></div>
    <div class="art">
      <div class="avatar"><slot name="avatar"></slot></div>
      <div class="info">
        <slot name="info"></slot>
      </div>
    </div>
    <el-divider></el-divider>
    <div class="content"><slot name="content"></slot></div>
  </div>
</template>

<script>
export default {
  name:'ArtDisplayItem'
}
</script>

<style scoped>
#artDisplayItem{
  border:1px solid #dfdfdf;
  border-radius: 3px;
  margin:5px 0;
}

.top-title{
    height:44px;
    line-height: 44px;
    padding-left:10px;
    border-radius: 3px;
    color:white;
    background-color: rgb(73, 156, 233);
  }

  .art{
    padding:20px 0 0 10px;
    display: flex;
    align-items: center;
  }

  .avatar img{
    width:90px;
    height:90px;
  }

 .content{
   padding:0 10px 20px;
   
 }
</style>


