<template>
    <div id="user">
        <user-top-find @findUser="findUser"></user-top-find>
        <user-list :content="user"></user-list>
    </div>
</template>

<script>
import UserTopFind from './userChildren/UserTopFind'
import UserList from './userChildren/UserList'

export default {
    name:'User',
    components:{
        UserTopFind,
        UserList
    },
    data(){
        return{
            user:[]
        }
    },
    created(){
        this.user = this.$store.state.user
    },
    methods:{
        findUser(name){
            let reg = /\s+/
            if(name === '' || reg.test(name)){
                this.$router.go(0)
            }else{
                this.$store.commit('findUser',name)
                console.log(this.$store.state.selectUser)
                this.user = this.$store.state.selectUser
            }
            
        }
    }
}
</script>

<style  scoped>
#user{
    width:80%;
}
</style>


